<script>
export default {
  data() {
    return {
      hasorder: false,
      expresspopup: false,
      list: [
        {
          id: 0,
          status: 0
        },
        {
          id: 1,
          status: 1
        },
        {
          id: 2,
          status: 2
        },
        {
          id: 3,
          status: 3
        }
      ]
    };
  },
  created() {
    if (this.hasorder == true) {
      document
        .querySelector("body")
        .setAttribute("style", "background-color: #FFF");
    }
  },
  methods: {
    togo() {
      this.$router.push({
        name: "orderDetails"
      });
    },
    actions(data) {
      if (data == 3) {
        this.expresspopup = true;
      }
    }
  },
  computed: {
    statusText: () => data => {
      if (data == 0) return "待付款";
      if (data == 1) return "待发货";
      if (data == 2) return "已完成";
      if (data == 3) return "待收货";
    },
    btnText: () => data => {
      if (data == 0) return "取消订单";
      if (data == 1) return "提醒发货";
      if (data == 2) return "删除订单";
      if (data == 3) return "查看物流";
    }
  }
};
</script>

