                  color: #ff4a54;
                  font-size: 28px;
                margin-right: 23px;

              }
              del{
                color: #999;
                  font-size: 24px;
              }
              
              
            }
          }
        }
      }
      footer {
        background-color: #fff;
        height: 100px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 26px;
        color: #101010;
        p {
          flex: 1;
          span{
            font-size:30px;
          }
        }
        div {
          color: #555;
          border: thin solid #D7D7D7;
          width: 140px;
          height: 58px;
          line-height: 58px;
          border-radius: 5px;
          text-align: center;
          margin-left: 20px;
        }
        .notpaid {
          color: #fff;
          background-color: #fd656d;
          border-color: #fd656d;
        }
      }
    }
  }
  // .expresspopup {
