<template>
  <div class="content">
    <van-tabs v-model="active" sticky color="#FF4A54" title-active-color="#FF4A54" :line-height="2">
      <van-tab title="全部">
        <list-order></list-order>
      </van-tab>
      <van-tab title="待付款">
        <list-order></list-order>
      </van-tab>
      <van-tab title="待收货">
        <list-order></list-order>
      </van-tab>
      <van-tab title="已完成">
        <list-order></list-order>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import listOrder from'@/web/components/ListOrder'
export default {
  components: {
    listOrder
  },
  data() {
    return {
      active: 0
    };
  }
};
</script>

<style lang="scss" scoped>
.content{
  padding-bottom: 77px;
}
</style>



